import { Button, ButtonGroup, Container, createStyles, Fade, Grow, makeStyles, Paper, Theme } from "@material-ui/core";
import Worlds from "./Worlds";
import { ButtonStyle, Config, Groups } from '../util/constants'
import { useState } from "react";

import ExpandMoreIcon from "@material-ui/icons/ExpandMore"

import ExpandLessIcon from '@material-ui/icons/ExpandLess'

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
           
        },
        container: {
            display: 'flex',
        },
        paper: {
            margin: theme.spacing(1),
        },
    }),
);

interface WorldBox_Props {
    handleWorldSelection: (g: string) => void,
    handleGroupSelection: (g: string) => void,
    config: Config,
    world: number
}
export default function WorldBox(props: WorldBox_Props) {
    const { handleWorldSelection, handleGroupSelection, config, world } = props;
    const [worldOpen, setWorldOpen] = useState(false)
    const [groupOpen, setGroupOpen] = useState(false)
    const classes = useStyles();

    const handleWorld = (w: boolean) => {
        setWorldOpen(!w)
        if (!w) {
            setGroupOpen(false)
        }
    }
    const handleGroup = (g: boolean) => {
        setGroupOpen(!g)
        if (!g) {
            setWorldOpen(false)
        }
    }
    const getStyle=()=>{
        if(!worldOpen && !groupOpen) {
           return  {
            width: '90%', margin: 'auto', height: '23'
        }} 
        return {
            width: '90%', margin: 'auto'
        }
        
    }
    return (
     
            <Paper style={getStyle()}>
                <ButtonGroup style={{marginTop:'1px', marginBottom:'2px'}} >
                    <Button key='world_button' 
                        onClick={() => handleWorld(worldOpen)}> Worlds
                        {worldOpen ? <ExpandLessIcon /> : <ExpandMoreIcon />} </Button>
                    <Button key='group_button' onClick={() => handleGroup(groupOpen)}>Groups
                        {groupOpen ? <ExpandLessIcon /> : <ExpandMoreIcon />}
                    </Button>
                </ButtonGroup>
                <div className={classes.container}>
                    <Fade key='world_Grow' in={worldOpen}>


                        <Worlds key="world_box" setWorld={handleWorldSelection} groups={config.Groups} name={'Worlds'} selected={world} />
                    </Fade>
                    <Fade key='group_box' in={groupOpen}>
                        <Worlds setWorld={handleGroupSelection} groups={config.Groups} name={'Groups'} />
                    </Fade>
                </div>
            </Paper>
     
    )
}
